<template lang="">
    <div class="banner">
<!--轮播结构-->
<div class="carousel" @mouseenter="enter" @mouseleave="leave">
    <transition-group
            tag="ul"
            name="image"
            enter-active-class="animated bounceInRight"
            leave-active-class="animated bounceOutLeft"
    >
        <li v-for='(image,index) in img' :key='index' v-show="index === mark">
            <a v-for="h in href" :href="h">
                <img :src="image">
            </a>
        </li>
    </transition-group>
    <div class="bullet">
        <span v-for="(item,index) in img.length" :class="{'active':index === mark}"
         @click="change(index)" :key="index"></span>
    </div>
    <!-- <div class="switch">
        <span class="prev" @click="prev">&lt;</span>
        <span class="next" @click="next">&gt;</span>
    </div> -->
</div>
    </div>
</template>

<script>
import "../assets/css/animate.min.css";
export default {
  el: ".banner",
  data() {
    return {
      mark: 0,
      href:[
          "https://ys.mihoyo.com/main/news/detail/20757",
          "https://ys.mihoyo.com/main/news/detail/20746",
          "https://ys.mihoyo.com/main/news/detail/20724",
          "https://ys.mihoyo.com/main/news/detail/20562",
      ],
      img: [
          require("../assets/img/home/b1.jpg"), 
          require("../assets/img/home/b2.jpg"), 
          require("../assets/img/home/b3.jpg"), 
          require("../assets/img/home/b4.jpg")
          ],
      time: null,
    };
  },
  methods: {
    //添加方法
    change(i) {
      this.mark = i;
    },
    prev() {
      this.mark--;
      if (this.mark === -1) {
        this.mark = 3;
        return;
      }
    },
    next() {
      this.mark++;
      if (this.mark === 4) {
        this.mark = 0;
        return;
      }
    },
    autoPlay() {
      this.mark++;
      if (this.mark === 4) {
        this.mark = 0;
        return;
      }
    },
    play() {
      this.time = setInterval(this.autoPlay, 5000);
    },
    enter() {
      console.log("enter");
      clearInterval(this.time);
    },
    leave() {
      console.log("leave");
      this.play();
    },
  },
  created() {
    this.play();
  },
};
</script>

<style>
li {
  list-style: none;
}
.carousel {
  width: 640px;
  height: 400px;
  overflow: hidden;
  /* margin: 100px auto; */
  position: relative;
}
.carousel ul {
  width: 100%;
  height: 100%;
}
.carousel ul li {
  position: absolute;
}
.carousel ul li a img {
  width: 640px;
  height: 400px;
}
.bullet {
  position: absolute;
  font-size: 0;
  bottom: 20px;
  left: 50%;
  margin-left: -42px;
}
.bullet span {
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 2px solid rgba(255, 255, 255, 0.685);
  margin-left: 15px;
  border-radius: 50%;
  cursor: pointer;
}
.bullet span:first-child {
  margin-left: 0;
}
.switch {
  color: azure;
}
.switch span {
  position: absolute;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.1);
  font-size: 20px;
  color: #ffffff;
  top: 50%;
  margin-top: -25px;
  cursor: pointer;
  font-family: "宋体";
}
.switch span:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
.prev {
  left: 0;
}
.next {
  right: 0;
}
.active {
  background-color: rgb(255, 255, 255) !important;
}
/*.image-enter-active{
            transform:translateX(0);
            transition:all 1s ease;
        }
        .image-leave-active{
            transform:translateX(-100%);
            transition:all 1s ease
        }
        .image-enter{
            transform:translateX(100%);
        }
        .image-leave{
            transform:translateX(0);
        }*/
</style>