  <div @mouseleave="leave()" class="nav">
    {{ poi() }}
    <div :class="{touch:true,touch_hide:touch_hide}"></div>
    <div class="music">
      <ul>
        <li v-for="m in music" :key="m.id">
          <audio v-if="mcid == m.id" id="music">
            <source :src="m.path" />
          </audio>
        </li>
      </ul>
      <img
        v-show="mistrue"
        @click="m1click()"
        class="m1"
        src="@/assets/img/m0.png"
        alt=""
      />
      <img
        v-show="!mistrue"
        @click="m2click()"
        class="m2"
        src="@/assets/img/m1.png"
        alt=""
      />
    </div>
    <div class="ys">
      <img src="@/assets//img/ys.png" alt="" />
    </div>
    <!--  -->
    <div
      @mouseenter="enter(index)"
      @click="click(index)"
      class="li"
      v-for="(item, index) in town"
      :key="item.id"
    >
      <router-link :class="{ click_text: cid == index }" :to="item.href">{{
        item.name
      }}</router-link>
    </div>
    <div class="heart">
      <a href="https://azure.rth1.me/">
      <span>成长关爱系统</span>
      <img src="./assets/img/chengzhangguanaixitong.png" alt="">
      </a>
    </div>
    <div class="user">
      <a href="http://357510o1t4.wicp.vip:25586/">
      <span>登录</span>
