<script>
export default {
  name: "nav",
  props: {},
  methods: {
    enter(index) {
      this.touch_hide=false;
      /*触摸li（类名）区块时，获取索引值，通过判断cid 使touch区块位于相应位置 */
      this.cid = index;
      if (this.cid == 0) {
        $(".touch").animate({ left: "310px" }, 150, "swing");
      }
      if (this.cid == 1) {
        $(".touch").animate({ left: "430px" }, 150, "swing");
      }
      if (this.cid == 2) {
        $(".touch").animate({ left: "550px" }, 150, "swing");
      }
      if (this.cid == 3) {
        $(".touch").animate({ left: "670px" }, 150, "swing");
      }
      if (this.cid == 4) {
        $(".touch").animate({ left: "790px" }, 150, "swing");
      }
      if (this.cid == 5) {
        $(".touch").animate({ left: "910px" }, 150, "swing");
      }
    },
    leave() {
      setTimeout(() => {
        /*刷新后回到首页位置，不知道怎么解决 */
        this.touch_hide=true;
      }, 200);
      /*离开nav区块时，通过判断clickcid的值 定义touch区块的位置 */
      if (this.clickcid == 0) {
        $(".touch").animate({ left: "310px" }, 150, "swing");
        this.cid = 0;
      }
      if (this.clickcid == 1) {
        $(".touch").animate({ left: "430px" }, 150, "swing");
        this.cid = 1;
      }
      if (this.clickcid == 2) {
        $(".touch").animate({ left: "550px" }, 150, "swing");
        this.cid = 2;
      }
      if (this.clickcid == 3) {
        $(".touch").animate({ left: "670px" }, 150, "swing");
        this.cid = 3;
      }
      if (this.clickcid == 4) {
        $(".touch").animate({ left: "790px" }, 150, "swing");
        this.cid = 4;
      }
      if (this.clickcid == 5) {
        $(".touch").animate({ left: "910px" }, 150, "swing");
        this.cid = 5;
      }
    },
    click(index) {
      /*点击li区块时，获取索引值并通过判断索引值 使该区块位于相应的位置 */
      this.clickcid = index;
      if (this.clickcid == 0) {
        $(".click").animate({ left: "310px" }, 1);
      }
      if (this.clickcid == 1) {
        $(".click").animate({ left: "430px" }, 1);
      }
      if (this.clickcid == 2) {
        $(".click").animate({ left: "550px" }, 1);
      }
      if (this.clickcid == 3) {
        $(".click").animate({ left: "670px" }, 1);
      }
      if (this.clickcid == 4) {
        $(".click").animate({ left: "790px" }, 1);
      }
      if (this.clickcid == 5) {
        $(".click").animate({ left: "910px" }, 1);
      }
    },
    m1click() {
      this.mistrue = false;
      music.play();
    },
    m2click() {
      this.mistrue = true;
      music.pause();
    },
    poi() {
      setInterval(() => {
        /*实现歌曲播放完自动播放下一首 */
        var x = document.getElementById("music").ended;
        if (x == true) {
          gohere:this.mcid++;
          console.log(this.mcid);
          if(this.mcid>3){
            this.mcid=0;
            setTimeout(() => {/*bukuishiwo */
              gohere;
            }, 1000);
          }
          setTimeout(() => {
            music.play();
          }, 1000);
        }
      }, 2000);
    },    
  },
  created() {
      var music = document.getElementById("music");
      this.mcid=Math.floor(Math.random() * 4);
      console.log(this.mcid)
    },
  data() {
    return {
      touch_hide:true,
      cid: 0 /*触摸时获取的索引值赋给的数据 */,
      clickcid: 0 /*点击时获取的索引值赋给的数据 */,
      mistrue: true,
      mcid: 0,
      town: [
        {
          id: 0,
          name: "首页",
          href: "/",
        },
        {
          id: 1,
          name: "新闻",
          href: "/News",
        },
        {
          id: 2,
          name: "角色",
          href: "/juese",
        },
        {
          id: 3,
          name: "世界",
          href: "/shijie",
        },
        {
          id: 4,
          name: "漫画",
          href: "/manhua",
        },
        {
          id: 5,
          name: "社区",
          href: "/shequ",
        },
      ],
      music: [
        {
          id: 0,
          path: require("@/assets/music/Mike Liu - Pure Sky 青空.mp3"),
        },
        {
          id: 1,
          path: require("@/assets/music/原神 - 璃月山（夜间） (钢琴演奏版).mp3"),
        },
        {
          id: 2,
          path: require("@/assets/music/恰空PurrLude - 原神-轻策庄（夜间） (钢琴演奏版).mp3"),
        },
        {
          id: 3,
          path: require("@/assets/music/Mike Liu - 绿华池.mp3"),
        },
      ],
    };
  },
};
</script>

